/*  about-us */

.about-us{
    color: black;
}

.about-us__title{
    margin-top: 144px;
    font-weight: 500;
    font-size: 36px;
}

.about-us__block {
    margin-top: 30px;
    display: flex;
    flex-direction: row;
}
.line-vertical::before{
    content: '';
    display: block;
    width: 8px;
    height: 100%;
    box-sizing: border-box;
    background-color: #FFA800;
}
.about-us__content{
    margin-left: 19px;
    font-size: 24px;
    line-height: 28px;
    font-weight: 400;
}

.about-us__content p:not(:first-child){
    margin-top: 1em;
}

/*how-its-work*/

.how-its-work{
    margin-top: 144px;
    background-image: url(../../img/how-its-work-img.png);
    background-color: #FFA800;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}

.how-its-work__part1{
    margin-top: 90px;
    margin-bottom: 107px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.how-its-work__link{
    font-size: 36px;
    line-height: 42px;
    font-weight: 500;
    text-decoration: none;
    color:#FFFFFF;
}

.how-its-work__know-more{
    height: 60px;
    font-size: 24px;
    line-height: 32px;
    font-weight: 700;
    padding-left: 75px;
    padding-right: 75px;

    border-radius: 3px;
    border: 1px solid #FFFFFF;
    background-color: transparent;
    color:#E5E5E5;
}

.how-its-work__know-more:hover {
    background-color: #FFCA62;
    color: #2D2B2F;
}

.how-its-work__know-more:focus {
    background-color: #FFFFFF;;
    color: #2D2B2F;
}

.how-its-work__part2{
    width: 906px;
    margin-top: 120px;
    margin-bottom: 90px;
    margin-left: auto;
    margin-right: auto;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.how-its-work__options{
    width: 226px;
    text-align: center;
}
 
.how-its-work__option1, .how-its-work__option2, .how-its-work__option3{
      margin-bottom:35px;
      display: flex;
      justify-content: center;
      margin-left: auto;
      margin-right: auto;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
}
.how-its-work__option1 img, .how-its-work__option2 img, .how-its-work__option3 img{
    width: 162px;
    height: 162px;
    margin-top: auto;
    margin-bottom: auto;
}
.how-its-work__option-description{
    font-size: 1.5em;
    font-weight: 300;
}


/*feedbacks*/


.feedbacks{
    color: #292929;
    margin-top: 132px;
    margin-bottom: 122px;
}
.feedbacks-title-h2 h2{
    font-weight: 500;
    font-size: 36px;
    line-height: 42px;
}

.feedbacks-items__item {
    display: flex;
    flex-direction: row;
}

.feedbacks-items__item__img {
    width: 200px;
}

.feedbacks-items__item__img img{
    border-radius: 50%;
}

.feedbacks-items__item__text{
    width: 400px;
    margin-left: 25px;
}

.feedbacks-items__item__text__title{
    margin-top: 10px;
    font-size: 24px;
    font-weight: 500;
}

.feedbacks-items__item__text-p{
    margin-top: 10px;
    font-size: 18px;
    font-style: italic;
    font-weight: 300;
    color: #928F94;
}

